.nav-container {
  max-width: 85vw;
  margin: auto auto 6% auto;
  font-family: var(--primFont);
}

.grid-nav {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  line-height: 12.5rem;
  text-transform: uppercase;
}

.font-nav {
  font-size: .9rem;
  font-weight: 700;
  text-align: center;
  padding: 1rem;
}

.menu-list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: .5rem;
}

.middle-brand {
  max-width: 7rem;
  margin: 0 auto;
  align-self: center;
}

.social-list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: .5rem;
}

.social-font {
  font-size: 1.3rem;
  text-align: center;
  padding: 1rem;
}

.nav-icon {
  cursor: pointer;
}

@media screen and (max-width: 768px) {
  .font-nav {
    font-size: .79rem;
  }

  .middle-brand {
    max-width: 6rem;
  }
}

@media screen and (max-width: 768px) {
  
  .middle-brand {
    margin-top: 10px;
    grid-row-start: 1;
  }

  .row2 {
    grid-row-start: 2;
  }

  .row3 {
    grid-row-start: 3;
  }

  .grid-nav {
    line-height: 2.5rem;
    grid-template-columns: 1fr ;

  }

  .menu-list {
    gap: 1.5rem;
  }

  .social-list {
    gap: 1.5rem;
  }
}
  
